

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Sansita', sans-serif;
  color: #908e8e;
  font-size: 20px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
  width: 100%;
  height: 80px;
  border-bottom: 1px solid #c3bbb9;
  display: block;
}

.navbar{
  position: relative;
  width: 78%;
  height: 100%;
  margin: auto;
  display: flex;
  align-content: stretch;
  align-items: center;
  justify-content: space-around;
}

.navbar a{
  position: relative;
  display: flex;
  justify-content: center;
  width: 33.33333%;
}

.logo{
  position: relative;
  width: 80%;
}

.navbar ul{
  position: relative;
  width: 33.33333%;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-content: stretch;
  align-items: center;
  justify-content: space-around;
  height: 100%;
}

.navbar li{
    position: relative;
    width: 100%;
    flex-grow: 1;
    display: flex;
    height: 100%;
    padding: 0 10px;
    min-height: 40px;
    align-items: center;
    text-align: center;
    justify-content: center;
    cursor:pointer;
}

.leftUl li{
  border-right: 1px solid #c3bbb9;
}

.rightUl li{
  border-left: 1px solid #c3bbb9;
}


.DopMenu{
  position: relative;
  margin: auto;
  width: 80%;
  height: 230px;
  border-bottom: 1px solid #c3bbb9;
  display: flex;
  justify-content: space-between;
  font-family: 'Cinzel', serif;
  font-family: 'Vollkorn', serif;
}

.DopMenu div{
  position: relative;
  font-size: 18px;
}

.DopMenu h4{
  padding-left: 10px;
  border-bottom: 1px solid #c3bbb9;
}

.DopMenu ul{
  list-style: none;
  padding-left: 10px;
}

.DopMenu li{
  cursor:pointer;
  position: relative;
  margin-bottom: 10px;
  list-style: none;
}

.default-link-div{
  border-right: 1px solid #c3bbb9;
}




